body {
  padding-right: 0px !important;
  overflow: overlay !important;
  color: rgb(55, 53, 47);
  background-color: #ffffff;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica,
    "Apple Color Emoji", Arial, sans-serif, "Segoe UI Emoji", "Segoe UI Symbol";
}

.MuiAutocomplete-popper > div {
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.12),
    0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
}

header {
  padding-right: 0px !important;
}

.linkButton {
  text-align: left;
}
.disabledBox:hover {
  background: #efeff0 !important;
}

.disabledBox {
  cursor: default !important;
}

.chooseBox:hover {
  cursor: pointer;
  background: #e4e5e6;
}
.chooseBox:focus {
  cursor: pointer;
  background: #dfe0e1;
}

.chooseBox {
  font-size: 15px;
  outline: none;
  min-width: 200px;
  transition: background fill 20ms ease-in 0s;
  border-radius: 3px;
  background: #efeff0;
  padding: 9px;
  border: 0;
}

.boxButton:hover {
  fill: #545454;
  background: rgba(202, 204, 206, 0.4);
}
.boxButton {
  user-select: none;
  transition: background fill 20ms ease-in 0s;
  cursor: grab;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 24px;
  border-radius: 3px;
  opacity: 1;
  fill: #bdbdbd;
  background: white;
}

.addFormButton:hover {
  color: #545454;
  fill: #545454;
  background: rgba(202, 204, 206, 0.4);
  cursor: pointer;
}

.addFormButton:focus {
  color: #545454;
  fill: #545454;
  background: rgba(202, 204, 206, 0.4);
}

.addFormButton {
  border-color: transparent;
  user-select: none;
  padding: 5px;
  width: 130px;
  transition: background fill 20ms ease-in 0s;
  border-radius: 3px;
  font-size: 13px;
  color: #bdbdbd;
  fill: #bdbdbd;
  background: white;
}

.nameInput > input {
  transition: background fill 20ms ease-in 0s;
  background: white;
  border-radius: 3px;
  padding: 8px;
  outline: none;
}

.checkBoxField {
  transition: background fill 20ms ease-in 0s;
  background: white;
  margin-left: 0 !important;
  margin-right: 0 !important;
  padding-bottom: 4px !important;
  padding-top: 4px !important;
  padding-left: 0px !important;
  padding-right: 12px !important;
}

/*
.fieldText:hover * {
  cursor: pointer;
}

.fieldText:hover textarea:focus {
  cursor: text;
}
*/

.fieldSelect:hover * {
  cursor: pointer;
}

.fieldSelect:hover input:focus {
  cursor: text;
}

.checkBoxField:hover {
  background: rgba(202, 204, 206, 0.4);
}

.nameInput > input:hover {
  background: rgba(202, 204, 206, 0.4);
}
.nameInput > input:focus {
  background: rgba(202, 204, 206, 0.4);
}

.propertyField {
  transition: background fill 20ms ease-in 0s;
  background: white;
  border-radius: 3px;
}
.propertyField:hover * {
  cursor: pointer;
}

.propertyField:hover input:focus {
  cursor: text;
}
.propertyField:hover {
  cursor: pointer;
  background: rgba(202, 204, 206, 0.4);
}

.propertyField .MuiSelect-select {
  padding-top: 12px !important;
  padding-bottom: 12px !important;
  padding-left: 4px !important;
}

.propertyField .MuiInputBase-root {
  cursor: initial;
  padding-right: 0px;
  flex-wrap: wrap;
}

.propertyField .MuiInputBase-root:hover {
  cursor: pointer;
}
.propertyField .MuiAutocomplete-input {
  width: 0;
  min-width: 0 !important;
}

.MuiAutocomplete-root[aria-expanded="true"] .propertyField {
  background-color: rgba(202, 204, 206, 0.4);
}
.propertyField .MuiAutocomplete-popupIndicator {
  display: none;
}

.propertyField .MuiOutlinedInput-notchedOutline {
  border: 0;
}

.propertyField input {
  font-size: 14px;
}
.collectionField input {
  font-size: 14px;
}

.collectionField {
  transition: background fill 20ms ease-in 0s;
  background: white;
  border-radius: 3px;
}

.collectionField:hover {
  cursor: pointer;
  background: rgba(202, 204, 206, 0.4);
}

.checkBox-small .MuiTypography-root {
  font-size: 14px;
}

.checkBox-small .MuiIconButton-root {
  padding-left: 4px;
}

.collectionField .MuiOutlinedInput-notchedOutline {
  border: 0;
}
.collectionField-empty .MuiOutlinedInput-notchedOutline {
  border: 1px solid yellow;
}

.collectionField:hover * {
  cursor: pointer;
}
.collectionField:hover input:focus {
  cursor: text;
}

button.MuiBox-root {
  outline: none;
}

.lockButton .MuiIconButton-root {
  transition: background fill 20ms ease-in 0s;
  color: #cacaca;
}

.lockButton .MuiIconButton-root:hover {
  color: #848484;
}

.checkBoxField {
  transition: background fill 20ms ease-in 0s;
  background: white;
  border-radius: 3px;
  padding: 8px;
  outline: none;
}

.checkBoxField:hover {
  cursor: pointer;
  background: rgba(202, 204, 206, 0.4);
}

.selectDataBox {
  width: 100%;
  max-width: 200px;
  min-height: 35px;
  transition: background fill 20ms ease-in 0s;
  background: white;
  border-radius: 3px;
}

.selectDataBox:hover {
  cursor: pointer;
  background: rgba(202, 204, 206, 0.4);
}

.editForm {
  min-height: 500px;
  min-width: 544px;
}

.boxButtonShowOnlyOnHover:hover {
  fill: #545454 !important;
  background: rgba(202, 204, 206, 0.4);
}

.boxButtonShowOnlyOnHover {
  user-select: none;
  transition: background fill 20ms ease-in 0s;
  cursor: grab;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 24px;
  border-radius: 3px;
  opacity: 1;
  fill: white;
  background: white;
}

.boxItemParent:hover .boxButtonShowOnlyOnHover {
  fill: #bdbdbd;
}

.headeViewerHr {
  background: #bdbdbd;
  width: 100%;
  border-style: ridge;
}

.titleDataOptionRow {
  font-weight: 400;
  font-size: 13px;
  margin-bottom: 2px;
}

.selectedDataOptionRow {
  font-size: 13px;
}

.notionPage {
  font-size: 13px;
  border-bottom: 1px solid rgba(55, 53, 47, 0.16);
}
.descriptionDataOptionRow {
  font-size: 11px;
}

.checkBox-small {
  height: 40px;
}

.checkBox-small .MuiCheckbox-root {
  padding-top: 0px;
  padding-bottom: 0px;
}
